body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 2s linear;
          animation: App-logo-spin infinite 2s linear;
  height: 20vmin;
  pointer-events: none;
  margin-bottom: 100px;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.title {
  font-size: 40px;
  color: black;
}

.buttons {
  width: 70%;
  padding-top: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.button{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 30px;
  height: 68px;
  width: 240px;
  border-radius: 5px;
  color: white;
  text-decoration: none;
}
.button:hover{
  opacity: 0.4;
}
.bias-button{
  background: -webkit-linear-gradient(left, rgba(9,9,121,1) 0%, rgba(0,212,255,1) 100%, rgba(2,0,36,1) 255%);
  background: linear-gradient(90deg, rgba(9,9,121,1) 0%, rgba(0,212,255,1) 100%, rgba(2,0,36,1) 255%);
}
.cg-button{
  background: -webkit-linear-gradient(left, rgba(255,0,0,1) 0%, rgba(121,9,9,1) 100%, rgba(2,0,36,1) 255%);
  background: linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(121,9,9,1) 100%, rgba(2,0,36,1) 255%);
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

